.IntroductionBlockParent {
  border-style: none;
}

.websiteContent {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  row-gap: 30px;
  column-gap: 30px;
  border-style: none;
  /* border: 1px solid red; */
}

.detailContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-style: none;
}

.HomeImageContainer1 {
  width: 100%;
  display: flex;
  justify-content: start;
  margin-top: 50px;
}

.HomeImageContainer {
  display: flex;
  justify-content: start;
  align-items: center;
  object-fit: cover;
  border-style: none;
  width: 60%;
  height: 100%;
}

.btnContainer {
  margin-right: 40px;
  justify-content: center;
  width: 100%;
  display: flex;
  flex-direction: column;

  padding: 20px;
}

.btn1 {
  font-weight: bold;

  width: 100%;
  height: 30px;
  border-radius: 4px;
  background-color: white;
  border: none;
  border: 1px solid var(--themeColor);
  color: var(--themeColor);
  font-size: 16px;
  display: flex;
  justify-content: start;
  text-align: center;
  align-items: center;
  white-space: nowrap;
    overflow: hidden;
    display: block;
    text-overflow: ellipsis;
}

.btn1:hover {
  background-color: var(--themeColor);
  color: white;
  cursor: pointer;
}

