  width: 100%;
}

.textHeading1 {
  padding-top: 90px;
  width: 180px;
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;

  /* margin-top: 100px;
  font-weight: bold;
  color: var(--themeColor);
  font-size: 24px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-transform: uppercase; */
  background-color: transparent;
  fill: var(--themeColor);
}

.textHeading2 {
  width: 50%;
  background-color: var(--themeColor);
  opacity: 0.8;
  margin-top: 50px;
  font-weight: bold;
  color: white;
  line-height: 55px;
  font-size: 40px;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

.textDetails {
  visibility: hidden;
  margin-top: 30px;
  color: var(--darkText);
  font-size: 18px;
  text-align: justify;
  line-height: 34px;
  font-weight: bolder;
  background-color: #e7efeb;
  border-radius: 10px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

@media (max-width: 1300px) {
  .IntroductionBlockParent {
    width: 100%;
  }
  .textHeading1 {
    font-size: 20;
    height: 80px;
    width: 80px;
  }
  .textHeading2 {
    width: 90%;
    margin-top: 10px;
    font-size: 30px;
    line-height: 40px;
  }

  .textDetails {
    margin-top: 10px;
    font-size: 18px;
    line-height: 30px;
  }
  .HomeImageContainer1 {
    margin-top: 10px;
  }

  .btnContainer {
    width: 100%;
    margin: 0;
  }

  .btn1 {
    font-size: 12px;
  }
  .btn2 {
    font-size: 12px;
  }
  .btn3 {
    font-size: 12px;
  }
  .btn4 {
    font-size: 12px;
  }
}
